.event-card {
  margin-top: 15px;
  border-width: 1px;
  border-style: solid;
  height: 100px;
  padding: 0;
  transition:height 0.3s;
  -webkit-transition:height 0.3s;
  text-align: left;
  /*border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  padding: 10px;*/
}

.event-card:hover {
  height: 120px;
}

.event-date {
  background-color: #7447B3;
  color: white;
  padding-top: 20px;
  height: 100%;
  float: left;
  width: 75px;
  font-size: 18px;
}

.event-title {
  padding-left: 90px;
  font-weight: bolder;
}

.event-url {
  color: black;
}

.event-url:hover {
  color: black;
}
